.accordion__item {
  border-top: 1px solid #333C72;
}
.accordion__item:last-child {
  border-bottom: 1px solid #333C72;
}
.accordion__item--active > .accordion__content {
  display: block;
}
.accordion__item--active > .accordion__trigger {
  padding-bottom: 20px;
  font-weight: bold;
}
.accordion__item--active > .accordion__trigger:after {
  top: 47%;
  transform: rotate(45deg);
}
.accordion__trigger {
  position: relative;
  padding: 31px 42px 33px;
  font-family: Rubik;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  color: #F2F2FF;
}
@media screen and (min-width: 768px) {
  .accordion__trigger {
    padding: 31px 45px;
    font-size: 18px;
    line-height: 72px;
  }
}
.accordion__trigger:after {
  content: "";
  display: inline-flex;
  position: absolute;
  top: 43%;
  left: 7px;
  width: 12px;
  height: 12px;
  border-top: 2px solid rgb(246, 37, 90);
  border-left: 2px solid rgb(246, 37, 90);
  transform: rotate(225deg);
  transition-duration: 0.5s;
}
.accordion__trigger--active:after {
  transform: rotate(45deg);
}
.accordion__content {
  display: none;
  padding-left: 42px;
  padding-bottom: 29px;
  font-family: Rubik;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  color: #F2F2FF;
}
@media screen and (min-width: 768px) {
  .accordion__content {
    padding-left: 101px;
    padding-bottom: 32px;
    font-size: 18px;
    line-height: 72px;
  }
}

