.header-banner {
    &__logo {
        display: block;
        margin-top: 36px;

        @include min-tablet {
            margin-top: 80px;
        }
    }
    &__title {
        max-width: 500px;
        margin-top: 17px;
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        color: $white;

        @include min-tablet {
            margin-top: 15px;
            font-size: 24px;
            line-height: 28px;
        }
    }

    &__description {
        max-width: 500px;
        margin-top: 14px;
        font-style: normal;
        font-weight: 300;
        font-size: 20px;
        line-height: 24px;
        color: $white;

        @include min-tablet {
            margin-top: 21px;
            font-size: 24px;
            line-height: 28px;
        }
    }

    &__datetime {
        display: flex;
        margin-top: 29px;
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 28px;
        color: $white;

        @include min-tablet {
            margin-top: 55px;
            font-size: 36px;
            line-height: 43px;
        }
    }

    &__date {
        margin-right: 27px;

        &::before {
            content: "";
            position: absolute;
            margin-left: 204px;
            height: 30px;
            border-left: 3px solid #f6255a;

            @include min-tablet {
                margin-left: 306px;
                height: 48px;
            }
        }

        @include min-tablet {
            margin-right: 51px;
        }
    }

    &__button {
        margin-top: 31px;

        @include min-tablet {
            margin-top: 57px;
        }
    }
}
